import { StorageService } from '../services/storage';
import { AIService } from '../services/api';

//  options.js  Logger
const Logger = {
  error: function(message, error) {
    console.error(`[ERROR] ${message}`, error);
  },
  info: function(message, data = {}) {
    console.info(`[INFO] ${message}`, data);
  }
};

class OptionsPage {
  constructor() {
    this.apiKeyInput = document.getElementById('apiKey');
    this.togglePasswordBtn = document.getElementById('togglePassword');
    this.saveApiKeyBtn = document.getElementById('saveApiKey');
    this.testConnectionBtn = document.getElementById('testConnection');
    this.messageDiv = document.getElementById('message');
    
    this.init();
  }
  
  async init() {
    try {
      const apiKey = await StorageService.getApiKey();
      if (apiKey) {
        this.apiKeyInput.value = apiKey;
      }
      
      this.bindEvents();
    } catch (error) {
      Logger.error('', error);
      this.showError('');
    }
  }
  
  bindEvents() {
    this.togglePasswordBtn.addEventListener('click', () => {
      const type = this.apiKeyInput.type;
      this.apiKeyInput.type = type === 'password' ? 'text' : 'password';
      this.togglePasswordBtn.textContent = type === 'password' ? '' : '';
    });
    
    this.saveApiKeyBtn.addEventListener('click', async () => {
      await this.saveApiKey();
    });
    
    this.testConnectionBtn.addEventListener('click', async () => {
      await this.testConnection();
    });
  }
  
  async saveApiKey() {
    try {
      const apiKey = this.apiKeyInput.value.trim();
      if (!apiKey) {
        return this.showError(' API Key');
      }
      
      await StorageService.saveApiKey(apiKey);
      this.showSuccess('API Key ');
    } catch (error) {
      Logger.error(' API Key ', error);
      this.showError('');
    }
  }
  
  async testConnection() {
    try {
      const apiKey = this.apiKeyInput.value.trim();
      if (!apiKey) {
        return this.showError(' API Key');
      }
      
      const aiService = new AIService(apiKey);
      await aiService.sendMessage('qwen2-72b', [
        { role: 'user', content: 'test' }
      ], () => {});
      
      this.showSuccess('');
    } catch (error) {
      Logger.error('', error);
      this.showError(' API Key ');
    }
  }
  
  showError(message) {
    this.messageDiv.className = 'error-message';
    this.messageDiv.textContent = message;
  }
  
  showSuccess(message) {
    this.messageDiv.className = 'success-message';
    this.messageDiv.textContent = message;
  }
}

// 
document.addEventListener('DOMContentLoaded', () => {
  new OptionsPage();
}); 